import React from 'react'
import { makeStyles } from "@material-ui/core/styles";
import Style from './SideBarStyle';
import SideBarMenuItems from "../../Constant"
import StrokeStyle from '../StrokeStyle'
import SelectionGroupPopup from '../SelectionGroupPopup'
import ClickAwayListener from '@material-ui/core/ClickAwayListener'
import inputMode from '../InputMode'
import ShapeLibrary from '../ShapeLibrary'
import TextModePopup from '../TextModePopup'
import AppInput from '../AppInput'
import { FaMousePointer, FaHandRock } from 'react-icons/fa';

const useStyles = makeStyles(Style);

const SideBarMenu = () => {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [open, setOpen] = React.useState(false);
    const [placement, setPlacement] = React.useState();
    const [subMenuIcons, setSubMenuIcons] = React.useState();
    const [selectedItemId, setSelectedItemId] = React.useState(inputMode.sideMenu);
    const [_, setSelectGroupVal] = React.useState(inputMode.selectionGroupVal);
    const classes = useStyles();
    
    const handleClick = (event, subMenus, newPlacement,id) => {
        if (id == inputMode.eraseAll) {
            inputMode.selectSideMenu(id);
            return;
        }
        setAnchorEl(event.currentTarget);
        //setOpen((prev) => placement !== newPlacement || !prev);
        var willOpen = false;
        if(id === selectedItemId) {
            setOpen(!open);
            willOpen = !open;
        }else {
            setOpen(true);
            willOpen = true;
        }

        if (willOpen && (id == inputMode.draw ||  id == inputMode.smartDraw || id == inputMode.shapes)) {
            AppInput.ignoreNextClick(true);
        } else {
            AppInput.ignoreNextClick(false);
        }

        setPlacement(newPlacement);
        setSubMenuIcons(subMenus)
        setSelectedItemId(id)
        inputMode.selectSideMenu(id);
    };

    const setSelectionMode = (val) => {
        inputMode.selectSideMenu(val);
        setSelectGroupVal(val);
    };
    
    const getIconAndName = (sidebar, classes) => {
        var {icon, name} = sidebar;
        const {menuItem} = classes;
        if (sidebar.id == inputMode.selectionGroup) {
            if (inputMode.selectionGroupVal == inputMode.selection) {
                icon = <FaMousePointer size="1.2em" color="#000000b8"/>;
                name = "Select";
            } else {
                icon = <FaHandRock size="1.2em" color="#000000b8"/>;
                name = "Move";
            }
        }
        return (
            <div>
                <div className={menuItem + ' ' + 'sideMenuIcons'}>
                    {icon}
                </div>
                <div className={menuItem +' '+ 'menuItems'}>{name}</div>
            </div>
        )
    }
    return (
        <ClickAwayListener onClickAway={() => setOpen(false)}>
        <div>
           { (selectedItemId === inputMode.selectionGroup) && <SelectionGroupPopup
                open={open}
                anchorEl={anchorEl}
                placement={placement}
                setOpenState={()=>setOpen(!open)}
                close={() => console.log('close')}
                setSelectionMode={setSelectionMode}
                selectedItemId={selectedItemId}
            /> }
           { (selectedItemId === inputMode.draw || selectedItemId === inputMode.smartDraw) && <StrokeStyle
                open={open}
                anchorEl={anchorEl}
                placement={placement}
                subMenuIcons ={subMenuIcons}
                setOpenState={()=>setOpen(!open)}
                close={() => console.log('close')}
                selectedItemId={selectedItemId}
            /> }
            { selectedItemId === inputMode.text && <TextModePopup
                open={open}
                anchorEl={anchorEl}
                placement="right-end"
            /> }
            { selectedItemId === inputMode.shapes && <ShapeLibrary
                open={open}
                anchorEl={anchorEl}
                placement="right-end"
                subMenuIcons ={subMenuIcons}
            /> }
            
            <div className={`${classes.sideBar} side_menus`}>
                {SideBarMenuItems.map((sidebar, index) => {
                    var itemClass = classes.sideBarMenu +' '+ 'sideBarMenu';
                    if (sidebar.id === selectedItemId) {
                        itemClass = classes.sideBarMenu +' '+ 'sideBarMenu active';
                    }
                    return (
                        <div className={itemClass} key={index} onClick={(event)=>handleClick(event, sidebar.subMenus,'right',sidebar.id)} >
                            {getIconAndName(sidebar, classes)}
                        </div>
                    )
                })}

            </div>
        </div>
        </ClickAwayListener>
        
    )
}
export default SideBarMenu